}

.root {
  height: 100vh;
  width: 100%;
  box-sizing: border-box;
}

.chat {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  display: flex;
}

.chat__menu-container {
  width: 350px;
  background: #ffffffb3;
  flex-shrink: 0;
}

.chat__subtitle {
  text-align: center;
  justify-self: center;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
}

.chat__menu {
  width: 100%;
  justify-content: space-around;
  display: flex;
  position: relative;
}

.chat__menu:after {
  content: "";
  width: 25%;
  height: 2px;
  background-color: #397387;
  display: block;
  position: absolute;
  bottom: 0;
}

.chat__menu--active-0:after {
  left: 0%;
}

.chat__menu--active-1:after {
  left: 25%;
}

.chat__menu--active-2:after {
  left: 50%;
}

.chat__menu--active-3:after {
  left: 75%;
}

.chat__icon {
  width: 50px;
}

.chat__container {
  width: 100%;
  box-sizing: border-box;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
}

.chat__container--dialog {
  justify-content: end;
}

.chat__main {
  padding: 0 5px;
}

.chat__messange, .chat__user-setting {
  width: 100%;
}

