  border-bottom: 1px solid #eaeaea;
  margin: 0;
  padding: 5px 0;
  display: flex;
  position: relative;
}

.dialogs-item__photo {
  width: 50px;
  height: 50px;
  object-fit: cover;
  border-radius: 50%;
  flex-shrink: 0;
  margin-right: 5px;
}

.dialogs-item__name {
  color: #397387;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  margin: 0 0 5px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 150%;
  display: -webkit-box;
  overflow: hidden;
}

.dialogs-item__messange {
  color: #999;
  text-overflow: ellipsis;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  margin: 0;
  font-family: Montserrat;
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  display: -webkit-box;
  overflow: hidden;
}

.dialogs-item__number {
  text-align: center;
  color: #397387;
  width: 20px;
  height: 20px;
  border: 1px solid #397387;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-family: Montserrat;
  font-size: 12px;
  font-style: normal;
  font-weight: 700;
  line-height: 100%;
  display: flex;
  position: absolute;
  bottom: 5px;
  right: 5px;
}

.dialogs-item__time {
  color: #999;
  font-family: Montserrat;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 126%;
}

